import { useEffect, useState } from "react";
import { ReactComponent as Resume } from "../../Images/resume.svg";
import "./header.css";
const Header = ({ children }) => {
  const [sH, SetSH] = useState(false);
  useEffect(() => {
    if (window.scrolly > 80) {
      SetSH(true);
    } else {
      SetSH(false);
    }
  });
  console.log(children);
  return (
    <div>
      <nav id="navbar-example2" className="navbar navbar-light bg-light">
        <a className="navbar-brand navLogo" href="/">
          <Resume width={24} />
          <span>Kanchan Kumar</span>
        </a>
        <ul className="nav nav-pills navMenuClass">
          <li className="nav-item">
            <a className="nav-link" href="#Home">
              Home
            </a>
          </li>
          <li className="nav-item">
            <a className="nav-link" href="#About">
              About
            </a>
          </li>
          <li className="nav-item dropdown">
            <a className="nav-link" href="#Project">
              Project
            </a>
          </li>
          <li className="nav-item dropdown">
            <a className="nav-link" href="#Contacts">
              Contacts
            </a>
          </li>
        </ul>
      </nav>
      <div data-spy="scroll" data-target="#navbar-example2" data-offset="0">
        <h4 id="Home">{children && children[0]}</h4>
        <p>...</p>
        <h4 id="About">{children && children[1]}</h4>
        <p>...</p>
        <h4 id="Project">{children && children[2]}</h4>
        <p>...</p>
        <h4 id="Contacts">{children && children[3]}</h4>
        <p>...</p>
      </div>
    </div>
  );
};
export default Header;
