import React, { useEffect, useState } from "react";
import { ReactComponent as ScrollTop } from "../../Images/scrollTop.svg";
export default function Scroll() {
  const [sh, ssh] = useState(false);
  const scrollHeight = (e) => {
    let scrTop = e.target.documentElement.scrollTop;
    let windowheight = window.innerHeight;
    if (scrTop > windowheight) {
      ssh(true);
    } else {
      ssh(false);
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", (e) => scrollHeight(e));
  }, []);

  const scrollTop = (e) => {
    window.scroll({
      top: 0
    });
  };
  return (
    <div className="App">
      <div></div>
      {sh ? (
        <span
          onClick={(e) => scrollTop(e)}
          style={{ position: "fixed", top: "100px" }}
        >
          {" "}
          <ScrollTop width="24" />
        </span>
      ) : (
        ""
      )}
    </div>
  );
}
